.jury-information {
  width: 100%;
  height: 100%;
  padding: 46px 30px;
  background-color: $color-sapphire-89;
  overflow-y: scroll;
  transform: scale(1.3);
  opacity: 0;
  transition: $trans-default;

  @include vp-767 {
    padding: 41px 15px;
  }
}

.jury-information__name {
  margin: 0 0 10px;
  font-size: 22px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;

  @include vp-1365 {
    margin-right: 10px;
  }

  @include vp-767 {
    margin-right: 0;
  }
}

.jury-information__specialty {
  margin: -3px 0 33px;
  line-height: 24px;
  color: $color-spindle;
  text-align: center;
  font-weight: 300;

  @include vp-1365 {
    margin-right: 12px;
  }

  @include vp-767 {
    margin-right: 0;
  }
}

.jury-information__achievement-list {
  font-size: 14px;
  line-height: 22px;
  list-style-type: disc;
  padding-left: 35px;
}

.jury-information__achievement-item {
  margin-bottom: 13px;
}
