.price-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 360px;
  padding: 60px 20px 58px;
  border: 7px solid $color-default-white;
  background-color: $color-default-white;
  z-index: 5;

  &:hover:not(:nth-child(2)),
  &:focus-within:not(:nth-child(2)),
  &.is-active {
    border-image: $gradient-border-tickets-promo;
    border-image-slice: 1;
    padding-bottom: 58px;

    .btn--price:not([disabled]) {
      color: $color-default-white;
      background: $gradient-button $color-resolution-blue;
      border: none;
      max-width: 205px;
      padding-top: 27px;
      padding-bottom: 27px;
    }

    .price-item__price {
      margin-bottom: 35px;
    }
  }

  @include vp-1365 {
    width: 440px;
    padding-top: 60px;

    &:hover:not(:nth-child(2)),
    &:focus-within:not(:nth-child(2)),
    &.is-active {
      padding-top: 60px;
      padding-bottom: 58px;

      .price-item__price {
        margin-bottom: 24px;
      }

      .btn--price:not([disabled]) {
        padding-top: 22px;
        padding-bottom: 22px;
      }
    }
  }

  @include vp-767 {
    width: 290px;
    padding-top: 45px;
    padding-bottom: 43px;

    &:hover:not(:nth-child(2)),
    &:focus-within:not(:nth-child(2)),
    &.is-active {
      padding-top: 45px;
      padding-bottom: 43px;

      .price-item__price {
        margin-bottom: 23px;
      }
    }
  }
}

.price-item--full {
  @include vp-1365 {
    padding: 58px 20px;

    &:hover:not(:nth-child(2)),
    &:focus-within:not(:nth-child(2)),
    &.is-active {
      padding-top: 58px;
    }

    .price-item__title {
      margin-bottom: 18px;
    }

    .price-item__description {
      margin-bottom: 72px;

      @include vp-767 {
        margin-bottom: 59px;
      }
    }
  }
}

.price-item__title {
  margin: 0 0 7px;
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
    margin-bottom: 14px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
  }
}

.price-item__description {
  margin: 0 0 68px;
  font-weight: 300;
  line-height: 24px;

  &--time {
    @include vp-1365 {
      color: $color-chambray;
    }
  }

  @include vp-1365 {
    margin-bottom: 74px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 63px;
  }

  span {
    position: relative;
    top: 0;
    right: 4px;
    color: $color-sapphire;
  }
}

.price-item__price {
  position: relative;
  margin: 0 0 35px;
  font-size: 60px;
  font-weight: 700;
  line-height: 90px;
  color: $color-egyptian-blue;
  z-index: 1;

  &::before {
    position: absolute;
    content: attr(data-price);
    top: 20px;
    left: -19px;
    color: $color-whisper;
    opacity: 0.48;
    font-size: 100px;
    line-height: 150px;
    z-index: -1;
    transform: translateY(-50%);
  }

  @include vp-1365 {
    margin-bottom: 24px;
  }

  @include vp-767 {
    margin-bottom: 23px;
    font-size: 48px;
    line-height: 72px;

    &::before {
      font-size: 80px;
      line-height: 120px;
      top: 16px;
      left: -14px;
    }
  }

  svg {
    @include vp-767 {
      width: 24px;
      height: 33px;
    }
  }
}

.price-item__button {
  max-width: 205px;
}
