.reviews__wrapper {
  position: relative;
  margin: 0 auto;
  max-width: 674px;
  padding: 120px 0;

  @include vp-1365 {
    padding: 101px 0 100px;
  }

  @include vp-767 {
    padding: 71px 0 70px;
  }
}

.reviews__title {
  margin: 0 0 46px;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  text-align: center;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    margin-bottom: 41px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin-bottom: 45px;
    transform: translateX(-1px);
  }
}

.reviews__swiper {
  max-width: 560px;
  box-shadow: 0 0 40px 0 rgba(28, 51, 116, 0.2);

  @include vp-1365 {
    max-width: 580px;
  }

  @include vp-767 {
    min-width: 226px;
    margin: 0 32px;
  }
}

.reviews__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.reviews__item {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto;
  gap: 20px 30px;
  margin: 0 auto;
  padding: 61px 55px 60px 62px;
  max-width: 560px;
  min-height: 259px;
  background-color: $color-resolution-blue;
  color: $color-default-white;

  @include vp-1365 {
    max-width: 580px;
    min-height: 249px;
    padding: 60px 67px 59px;
    gap: 11px 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    grid-template-rows: 68px auto 1fr;
    gap: 10px;
    justify-items: center;
    padding: 30px 25px 29px;
    min-height: 230px;
  }

  @include vp-399 {
    min-height: 328px;
  }

  div {
    grid-column: 1/2;
    grid-row: 1/3;
    margin-top: -1px;
    margin-left: -2px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: $color-resolution-blue;

    @include vp-1365 {
      margin-top: 0;
    }

    @include vp-767 {
      width: 68px;
      height: 68px;
      margin: 0;
      grid-row: 1/2;
    }
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }

  p:nth-child(2) {
    margin: 0;
    font-size: 22px;
    font-weight: 400;
    line-height: 22px;

    @include vp-1365 {
      font-size: 20px;
      line-height: 20px;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 20px;
    }
  }

  p:last-child {
    margin: 0;
    line-height: 24px;
    font-weight: 300;

    @include vp-767 {
      text-align: center;
      font-size: 14px;
      line-height: 22px;
      padding-top: 7px;
    }
  }
}

.reviews__button {
  position: absolute;
  width: 56px;
  height: 56px;
  border: none;
  background-color: $color-transparent;
  cursor: pointer;

  &:not([disabled]):hover,
  &:not([disabled]):focus {
    border-radius: 50%;
    border: 1px solid $color-sapphire;
  }

  &:not([disabled]):active {
    border-radius: 50%;
    background-color: $color-sapphire;

    svg {
      color: $color-default-white;
    }
  }

  &[disabled] {
    opacity: 0.3;
    pointer-events: none;
    cursor: default;
  }

  &--prev {
    top: 53.8%;
    left: -16px;

    svg {
      left: 25px;
      top: -7px;
    }
  }

  &--next {
    top: 53.5%;
    right: -18px;
    transform: rotate(180deg);

    svg {
      right: 11px;
      top: -7px;
    }
  }

  @include vp-1365 {
    &--prev {
      left: -19px;
      top: 52.6%;
    }

    &--next {
      right: -21px;
      top: 52.3%;
    }
  }

  @include vp-767 {
    width: 30px;
    height: 40px;

    &--prev {
      left: -6px;
      top: 53.3%;

      svg {
        left: 14px;
        top: 2px;
      }
    }

    &--next {
      right: -6px;
      top: 53.3%;

      svg {
        right: 6px;
        top: 0;
      }
    }

    &:not([disabled]):hover,
    &:not([disabled]):focus {
      border: none;
    }

    &:not([disabled]):active {
      background-color: $color-transparent;

      svg {
        color: $color-sapphire;
      }
    }
  }

  svg {
    color: $color-sapphire;
    position: absolute;
    transform: translate(-50%, 50%);

    @include vp-767 {
      width: 10px;
      height: 19px;
    }
  }
}
